<template>
    <div class="content--wrapper">
        <div class="grid">
            <div class="row">
                <div class="col pc-3">
                    <div class="list-message">
                        <ul class="message__list">
                            <li v-on:click="getUser(user)" v v-if="user.id !== userCurrent.uid" v-bind:key="user.id" class="message__item" v-for="user in listUser">
                                <img  v-bind:src="user.avatar">
                                <div class="message-info">
                                    <p>{{user.name}}</p>
                                    <p class="mess">{{user.email}}</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
                <div v-if="isMessage" class="col pc-9">
                    <div class="header">
                        <div class="header-info">
                            <img v-bind:src="userMessage.avatar" alt="">
                            <div class="header-text">
                                <h1 v-if="userMessage">{{userMessage.name}}</h1>
                                <p>Hoạt động trên Message</p>
                            </div>
                        </div>
                    </div>
                    <div class="board">
                        <ul class="board__list">
                            <li  class="board__item"  v-for=" (item, index) in listMessage" v-bind:key="index">
                                <div class="item" v-bind:class="{margin:item.id === userCurrent.uid}">
                                    <img v-if="item.id !== userCurrent.uid" v-bind:src="item.avatar" alt="">
                                    <p v-bind:class="{active:item.id === userCurrent.uid}">{{item.mess}}</p>
                                </div>
                            </li>
                        </ul>
                    </div>
                    
                        <form action="" class="value-message">
                            <input v-model="value" type="text" placeholder="Nhập tin nhắn">
                        <img v-on:click="sendMessage" src="data:image/png;base64," alt="">
                        </form>
                </div>
                <div v-if="!isMessage" class="col pc-9">
                    <div class="message-loading">
                        <h1> Xin chào {{userCurrent.displayName}}</h1>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Firebase from '../../Firebase'
import md5 from 'md5'
export default {
    data() {
        return {
            userCurrent:JSON.parse(window.localStorage.getItem('user')),
            listUser:[],
            value:'',
            userMessage:null,
            listMessageGetDatabase:[],
            groupChat:'',
            listMessage:[],
            groupChatId:'',
            isMessage:false
        }
    },
    methods:{
        getListUser(){
            Firebase.database()
            .ref('users')
            .once('value')
            .then((snap) =>{
                this.listUser=[]
                console.log("Ok");
                snap.forEach((item) =>{
                
                    
                    this.listUser.push({...item.val()})
                })
            })
        },
        getUser(user){
            this.isMessage=true
            this.userMessage=user
            this.getRoom()
            this.isMessage=true
        },
        sendMessage(event){
            event.preventDefault();
            if(this.value.length >=1){
                Firebase.database()
                .ref('messages')
                .child(this.groupChat)
                .push()
                .set({
                    mess:this.value,
                    id:this.userCurrent.uid,
                    time:Firebase.database.ServerValue.TIMESTAMP,
                    avatar:this.userCurrent.photoURL
                })
                .then(() =>{
                    this.value=''
                    this.getRoom()
                    const box = document.querySelector('.board');
                    box.scrollTop=box.scrollHeight
                    
                })
                .catch((err) =>{
                    console.log(err);
                })
            }
            else{
                alert("Bạn vui lòng nhập tin nhắn")
            }
        },
        hashString(string){
            return string = md5(string)
        },
        getRoom(){
            if(this.hashString(this.userCurrent.uid ) <= this.hashString(this.userMessage.id)){
                this.groupChat=`${this.userCurrent.uid}+${this.userMessage.id}`
            }
            else{
                this.groupChat = `${this.userMessage.id}+${this.userCurrent.uid}`
            }
            console.log(this.hashString(this.userCurrent.uid ) <= this.hashString(this.userMessage.id));   
            const tpm=[]
            Firebase.database()
            .ref('messages')
            .child(this.groupChat)
            .on('child_added',snap =>{
                    tpm.push(snap.val())
                    this.listMessage=tpm
                })
        }
    },
    created() {
        this.getListUser()
        
    }
}
</script>

<style>
.header{
    padding: 5px 0;
}
.header-info{
    display: flex;
    align-items: center;
}
.header-text {
    margin-left: 16px;
    
}
.header-text h1{
    margin: 0;
    padding: 0;
    text-align: left;
    margin-bottom: 5px;
    font-size: 20px;
}
.header-text p{
    margin: 0;
    padding: 0;
    color: #cccccc;
}
.header-info{
    display: flex;
    padding: 10px 0;
}
.header-info img{
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.message-loading{
    width: 100%;
    height: calc(100vh - 63px);
    display: flex;
    align-items: center;
    justify-content: center;
}
.message-loading h1{
    font-size: 40px;
}
.margin.item{
    margin-left: 719px;
    display: flex;
    flex-direction: row-reverse;
}
.item{
    display: flex;
}
.board__item .active{
    background-color: red;
}
.board__item{
    display: flex;
    
}
.board__item p{
    width: 400px;
    background: #ffffff;
    box-shadow: 1px 2px 3px #cccccc;
    border-radius: 5px;
    padding: 8px 5px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 1px;
    margin-bottom: 2px;
    
}
.board__item img {
    width: 40px;
    height: 40px;
    object-fit: cover;
    border-radius: 50%;
    margin: 10px 10px;
    margin-top: 20px;
}
.board__list{
    list-style: none;
    margin: 0;
    padding: 0;
}
.value-message{
    display: flex;
    width: 100%;
    height: 40px;
}
.value-message input{
    width: 100%;
    height: 40px;
    padding: 0 20px;
    font-size: 25px;
}
.value-message img{
    width: 30px;
    height: 30px;
    padding: 10px 20px;
    display: block;
    cursor: pointer;
}
.value-message img:hover{
    background: blue;
}
.header{
    width: 100%;
    border-bottom: 1px solid #cccccc;
}
.board{
    width: 100%;
    height: 530px;
    border: 1px solid #cccccc;
    overflow-y: scroll;
    overflow-x: hidden;
}
.message__item{
    display: flex;
    padding: 10px 15px;
    border-radius: 10px;
    cursor: pointer;
}
.message__item:hover{
    background: #cccccc;
}
.message__item p{
    margin: 0;
    padding: 0;
    margin: 4px 10px;
    font-size: 18px;
    font-weight: 800;
}
.message__item .mess{
    font-size: 16px;
    font-weight: 100;
}
.message__item img{
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
}
.message__list{
    margin: 0;
    padding: 0;
    height: calc(100vh - 60px);
}
.list-message{
    background-color: white;
    width: 100%;
    height: calc(100vh - 64px);
    box-shadow: 1px 2px 3px rgb(100, 96, 96);
    border-radius: 3px;
}
</style>


